import React from "react"
import { Link } from "gatsby"
import scrollTo from "gatsby-plugin-smoothscroll"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Home from "../components/home"

import contactGIF from "../gifs/contact.gif"
import projectsGIF from "../gifs/projects.gif"
import whoamiGIF from "../gifs/whoami.gif"
import Whoami from "./whoami"
import Projects from "./projects"

import Footer from "../components/footer"
import indexStyle from "./index.module.scss"
import whoamiStyle from "./whoami.module.scss"
import projectsStyle from "./projects.module.scss"


export const query = graphql`
  query {
    allContentfulProject(sort: { fields: Date, order: DESC }) {
      edges {
        node {
          title
          url
          organization
          body {
            body
          }
          Date(formatString: "MMMM, YYYY")
          image {
            resize(width: 300, height: 300) {
              src
            }
          }
        }
      }
    }
  }
`

const IndexPage = props => {
  return (
    <div>
      <Layout>
        <main>
          <div className={indexStyle.container}>
            <Home />
            <div className={indexStyle.navi}></div>
          </div>
          <div id="projects">
            <div>
              <h2 className={projectsStyle.title}>Projects</h2>
              <ol className={projectsStyle.projects}>
                {props.data.allContentfulProject.edges.map(edge => {
                  return (
                    <section className={projectsStyle.projectWrapper}>
                      <li>
                        <div className={projectsStyle.projectContent}>
                          <div className={projectsStyle.container}>
                            <div className={projectsStyle.left}>
                              <h2 className={projectsStyle.projectName}>
                                {edge.node.title}
                              </h2>
                              <p className={projectsStyle.body}>
                                {edge.node.body.body}
                              </p>
                            </div>
                            <div className={projectsStyle.right}>
                              <div className={projectsStyle.contents}>
                                <img
                                  className={projectsStyle.img}
                                  src={edge.node.image.resize.src}
                                  art={edge.node.title}
                                ></img>
                              </div>
                            </div>
                          </div>
                        </div>
                      </li>
                    </section>
                  )
                })}
              </ol>
            </div>
          </div>
        </main>
      </Layout>
    </div>
  )
}
 
export default IndexPage;